  width: 100%;
  background-size: 100% 100%;
  text-align: center;
  position: relative;
}
.teacher-introduce-header .teacher-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
}
.teacher-introduce-header .teacher-bg img {
  width: 100%;
  height: 100%;
  filter: blur(3px);
}
.teacher-introduce-header .avatar {
  width: 100%;
  height: 65px;
  text-align: center;
  position: absolute;
  left: 0;
  bottom: -32.5px;
}
.teacher-introduce-header .avatar img {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  border: 2px solid #FFFFFF;
}
.teacher-introduce-content {
  padding: 32.5px 10px 10px 10px;
}
.teacher-introduce-content .img-fullwidth {
  font-size: 15px;
  line-height: 2em;
  color: #323232;
}
.teacher-introduce-content .img-fullwidth hr {
  margin: 10px 0;
  margin: 5px 0;
  height: 0px;
  border: none;
  border-top: 1px solid #e6e6e6;
}
.teacher-introduce-content img {
  max-width: 100%;
  margin: 5px 0;
}
