.weui-loading {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  animation: weuiLoading 1s steps(12, end) infinite;
  background: transparent url("data:image/svg+xml;base64,") no-repeat;
  background-size: 100%;
}
.weui-loading.weui-loading_transparent {
  background-image: url("data:image/svg+xml;base64,");
}
@-webkit-keyframes weuiLoading {
0% {
    transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    transform: rotate3d(0, 0, 1, 360deg);
}
}
@keyframes weuiLoading {
0% {
    transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    transform: rotate3d(0, 0, 1, 360deg);
}
}
.weui-loadmore {
  width: 65%;
  margin: 1.5em auto;
  line-height: 1.6em;
  font-size: 14px;
  text-align: center;
}
.weui-loadmore__tips {
  display: inline-block;
  vertical-align: middle;
}
.weui-loadmore_line {
  border-top: 1px solid #E5E5E5;
  margin-top: 2.4em;
}
.weui-loadmore_line .weui-loadmore__tips {
  position: relative;
  top: -0.9em;
  padding: 0 .55em;
  color: #999999;
}
.weui-loadmore_dot .weui-loadmore__tips {
  padding: 0 .16em;
}
.weui-loadmore_dot .weui-loadmore__tips:before {
  content: " ";
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #E5E5E5;
  display: inline-block;
  position: relative;
  vertical-align: 0;
  top: -0.16em;
}
.vux-loadmore.weui-loadmore_line {
  display: flex;
  border-top: 0;
}
.vux-loadmore.weui-loadmore_line:before,
.vux-loadmore.weui-loadmore_line:after {
  position: relative;
  top: -1px;
  flex: 1;
  content: '';
  border-top: 1px solid #E5E5E5;
}

.vux-divider {
  display: table;
  white-space: nowrap;
  height: auto;
  overflow: hidden;
  line-height: 1;
  text-align: center;
  padding: 10px 0;
  color: #666;
}
.vux-divider:after,.vux-divider:before {
  content: '';
  display: table-cell;
  position: relative;
  top: 50%;
  width: 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,)
}
.vux-divider:before {
  background-position: right 1em top 50%
}
.vux-divider:after {
  background-position: left 1em top 50%
}

.contrast-top {
  width: 100%;
  left: 0;
  top: 0;
  -webkit-box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.27);
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.27);
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  z-index: 3;
  font-size: 1em;
  background: #FFFFFF;
  padding-left: 15px;
  padding: 10px 15px;
}
.contrast-top p {
  color: #666666;
}
.contrast-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url(/static/img/neza/org/duibi-BG.jpg) repeat-y;
  background-size: contain;
  overflow-x: hidden;
  overflow-y: auto;
}
.contrast-content {
  width: 100%;
  left: 0;
  z-index: 2;
  padding: 10px;
  overflow-x: hidden;
  overflow-y: auto;
  box-sizing: border-box;
}
.contrast-content img {
  width: 48%;
}
span.ability-title {
  display: inline-block;
  background: #FFFFFF;
  padding: 0 12px;
  border-radius: 3px;
}
.content-preview {
  padding: 10px;
  width: 100%;
  background: url(/static/img/neza/org/duibi-BG.jpg) repeat-y;
  background-size: contain;
  box-sizing: border-box;
}
.content-preview img {
  width: 48%;
}

.ivu-progress {
  display: inline-block;
  width: 100%;
  font-size: 12px;
  position: relative;
}
.ivu-progress-outer {
  display: inline-block;
  width: 100%;
  margin-right: 0;
  padding-right: 0;
}
.ivu-progress-show-info .ivu-progress-outer {
  padding-right: 20px;
  margin-right: -20px;
  box-sizing: border-box;
}
.ivu-progress-inner {
  display: inline-block;
  width: 100%;
  background-color: #f3f3f3;
  border-radius: 100px;
  vertical-align: middle;
}
.ivu-progress-bg {
  border-radius: 100px;
  background-color: #2db7f5;
  transition: all .2s linear;
  position: relative;
}
.ivu-progress-text {
  display: inline-block;
  margin-left: 5px;
  text-align: left;
  font-size: 1em;
  vertical-align: middle;
}
.ivu-progress-active .ivu-progress-bg:before {
  content: '';
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  border-radius: 10px;
  animation: ivu-progress-active 2s ease-in-out infinite;
}
.ivu-progress-wrong .ivu-progress-bg {
  background-color: #ed3f14;
}
.ivu-progress-wrong .ivu-progress-text {
  color: #ed3f14;
}
.ivu-progress-success .ivu-progress-bg {
  background-color: #19be6b;
}
.ivu-progress-success .ivu-progress-text {
  color: #19be6b;
}
@keyframes ivu-progress-active {
0% {
    opacity: .3;
    width: 0;
}
100% {
    opacity: 0;
    width: 100%;
}
}

